import * as React from 'react';
import { AnyAction } from 'redux';
import ArticleModel from '../../models/article';
import Loading from '../../shared/Loading';
import formatDate from '../../shared/formatDate';

interface ArticleProps {
  article: ArticleModel;
  fetchArticle: (id: string) => AnyAction;
  id: string;
  isLoading: boolean;
}

class Article extends React.Component<ArticleProps> {
  componentDidMount(){
    this.props.fetchArticle(this.props.id);
  }
  render() {
    if (this.props.isLoading){
      return <Loading />;
    }
    return (
      <article>
        <span className="article-date">{formatDate(this.props.article.date)}</span>
        <h1 className="article-title">{this.props.article.title}</h1>
        <div className="article-content" dangerouslySetInnerHTML={{__html: this.props.article.content}} />
      </article>
    );
  }
}

export default Article;



